.input-style {
  background-color: #161616;
  border: none;
  border-bottom: 1px solid #1d1d1d;
  border-radius: 0;
  font-size: 15px;
  height: 44px;
  padding: 9px 14px;
  width: 100%;
  color: var(--light);
}

.input-style::placeholder {
  color: #777;
}
.input-style:focus {
  outline: none;
  border-bottom: 1px solid var(--secondary-color);
}
