.contact-container h4 {
  font-size: 1.5rem;
}
.contact-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding-top: 50px;
  position: relative;
}
.contact-divider {
  width: 100px;
  height: 4px;
}

.contact-para {
  font-size: 15px;
}
.contact-column-divider {
  width: 3px;
  height: 300px;
  background-color: var(--secondary-color);
}
.contact-info-main p,
.contact-icons {
  line-height: 0.5rem;
}
.info-container {
  padding: 0 20px;
  border-left: 3px solid var(--secondary-color);
  margin-left: 20px;
}
.info-container a {
  color: #666;
}
@media only screen and (max-width: 768px) {
  .contact-icons {
    transform: scale(0.6);
  }
  .info-container a {
    font-size: 11px;
  }
}
